.board {
  text-align: center;
  font-size: 20px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.board .instructions {
  margin: 20px;
}

.board table {
  margin: 0 auto;
  border-spacing: 1px;
}

.board td {
  width: 50px;
}

.board td:after {
  content: '';
  display: block;
  margin-top: 100%;
}

.colour1 {
  background-color: #76a3f5;
}

.colour2 {
  background-color: #daa885;
}

.colour3 {
  background-color: #5cbd87;
}

.colour4 {
  background-color: #fff294;
}

.colour5 {
  background-color: #b884f9;
}

.colour6 {
  background-color: #fecfff;
}



